<script>
export default {
  name: 'GXLedgerAccountSearchItem',
  data() {
    return {}
  },
  props: {
    ledgerAccounts: {
      type: Array,
      default: null,
    },
    level: {
      type: Number,
      default: 1,
    },
  },
  methods: {
    getNumberOfAccountsByParts(ledgerAccount) {
      return ledgerAccount.number.split('-')
    },
    getSubAccountsCount(ledgerAccount) {
      if (ledgerAccount.subAccounts === undefined) return 0
      if (ledgerAccount.subAccounts === null) return 0
      return ledgerAccount.subAccounts.length
    },
    onSelected(ledgerAccount) {
      if (this.getSubAccountsCount(ledgerAccount) === 0)
        this.$emit('onSelected', ledgerAccount)
    },
  },
}
</script>

<template>
  <ul>
    <li
      v-for="ledgerAccount in ledgerAccounts"
      :key="ledgerAccount.ledgerAccountId"
      @click.prevent.stop="onSelected(ledgerAccount)"
    >
      <div
        class="group flex align-middle rounded pt-1 pb-1"
        :class="{
          'cursor-pointer': getSubAccountsCount(ledgerAccount) === 0,
          'hover:bg-ledgerAccountlist-hover':
            getSubAccountsCount(ledgerAccount) === 0,
        }"
      >
        <div class="inline-flex text-white ml-3">
          <span
            class="pt-1 pb-2px mr-px font-bold text-2xs"
            v-for="(item, idx) in getNumberOfAccountsByParts(ledgerAccount)"
            :class="[
              'bg-ledgerAccountTypes-' + ledgerAccount.ledgerAccountType.type,
              {
                'rounded-tl rounded-bl pl-2 pr-1': idx === 0,
                'pl-1 pr-2 rounded-r':
                  idx ===
                    getNumberOfAccountsByParts(ledgerAccount).length - 1 &&
                  !getSubAccountsCount(ledgerAccount) > 0,
                'pl-2 pr-2':
                  (idx !== 0 &&
                    idx !==
                      getNumberOfAccountsByParts(ledgerAccount).length - 1) ||
                  (idx !== 0 &&
                    idx ==
                      getNumberOfAccountsByParts(ledgerAccount).length - 1 &&
                    getSubAccountsCount(ledgerAccount) > 0),
              },
            ]"
            :key="idx"
          >
            {{ item }}
          </span>
        </div>
        <div
          class="inline-flex pl-5 pr-2 pt-2px sm:hover:bg-secondary-20 w-full"
        >
          <div class="inline-flex">
            <span
              class="whitespace-no-wrap truncate text-xs"
              :class="{
                'font-bold': getSubAccountsCount(ledgerAccount) > 0,
              }"
            >
              {{ ledgerAccount.name }}</span
            >
          </div>
        </div>
      </div>
      <GXLedgerAccountSearchItem
        v-if="getSubAccountsCount(ledgerAccount) > 0"
        :ledgerAccounts="
          ledgerAccount.subAccounts === null ? [] : ledgerAccount.subAccounts
        "
        @onSelected="onSelected"
        :level="level + 1"
      />
    </li>
  </ul>
</template>

<style>
.contenido {
  white-space: nowrap;
  text-overflow: ellipsis;
}
</style>
