<script>
import GXLedgerAccountSearchItem from '@components/_CoreTaxes/LedgerAccounts/GXLedgerAccountSearchItem'
export default {
  name: 'GxLedgerAccountSearch',
  data() {
    return {
      valueOfSearch: '',
      ledgerAccountsTemp: null,
      time: null,
      ledgerAccount: {
        ledgerAccountId: '',
        number: '',
        name: '',
        nature: '',
        ledgerAccountType: {
          type: '',
        },
      },
    }
  },
  props: {
    ledgerAccounts: {
      type: Array,
      default: null,
      required: true,
    },
  },
  components: {
    GXLedgerAccountSearchItem,
  },
  created() {
    if (this.time) {
      clearTimeout(this.time)
    }
    this.time = setTimeout(() => {
      if (this.ledgerAccount.ledgerAccountId === '') {
        this.$refs.inputLedgerAccount.focus()
        this.ledgerAccountsTemp = null
      }
    }, 15)
    this.$once('hook:beforeDestroy', () => {
      if (this.time) {
        clearTimeout(this.time)
      }
    })
  },
  mounted() {
    const clickOutside = (evt) => {
      if (!this.$el.contains(evt.target)) {
        this.ledgerAccountsTemp = null
      }
    }
    document.addEventListener('click', clickOutside)
    this.$once('hook:beforeDestroy', () => {
      document.removeEventListener('click', clickOutside)
    })
  },
  methods: {
    filteredList(e) {
      if (e.keyCode === 27) return
      if (this.ledgerAccount.name === '') this.resetLedgerAccount()

      if (this.time) {
        clearTimeout(this.time)
      }
      this.time = setTimeout(() => {
        if (this.ledgerAccount.name !== '') {
          this.ledgerAccountsTemp = this.searchList(
            JSON.parse(JSON.stringify(this.ledgerAccounts))
          )
        } else {
          this.ledgerAccountsTemp = JSON.parse(
            JSON.stringify(this.ledgerAccounts)
          )
        }
      }, 300)
      this.$once('hook:beforeDestroy', () => {
        if (this.time) {
          clearTimeout(this.time)
        }
      })
    },
    searchList(list) {
      let listReturn = Array()
      list.forEach((item) => {
        let keep = false
        if (
          item.name
            .toLowerCase()
            .indexOf(this.ledgerAccount.name.toLowerCase()) > -1 ||
          item.number
            .replace('-', ' ')
            .toLowerCase()
            .indexOf(
              this.ledgerAccount.name
                .toLowerCase()
                .replace('-', ' ')
                .replace('.', ' ')
            ) > -1
        )
          keep = true

        if ((item.subAccounts || []).length > 0)
          item.subAccounts = this.searchList(item.subAccounts)

        if ((item.subAccounts || []).length > 0 || keep) listReturn.push(item)
      })
      return listReturn
    },
    onSelected(ledgerAccount) {
      this.ledgerAccount = ledgerAccount
      this.ledgerAccountsTemp = null
      this.$emit('selectLedgerAccount', this.ledgerAccount.ledgerAccountId)
      this.$refs.inputLedgerAccount.focus()

      // this.$nextTick(() =>
      //   this.$refs[this.ledgerAccount.ledgerAccountId].focus()
      // )
    },
    getNumberOfAccountsByParts(ledgerAccount) {
      return ledgerAccount.number.split('-')
    },
    resetLedgerAccount() {
      this.ledgerAccount.ledgerAccountId = ''
      this.ledgerAccount.number = ''
      this.ledgerAccount.nature = ''
      this.ledgerAccount.ledgerAccountType.type = ''
      this.$emit('selectLedgerAccount', this.ledgerAccount.ledgerAccountId)
    },
    closeList(e) {
      if (e.keyCode !== 9) {
        e.stopPropagation()
        e.preventDefault()
      }
      if (this.time) {
        clearTimeout(this.time)
      }
      this.ledgerAccountsTemp = null
    },
    onFocus() {
      if (this.ledgerAccount.name === '') {
        this.ledgerAccountsTemp = JSON.parse(
          JSON.stringify(this.ledgerAccounts)
        )
        this.resetLedgerAccount()
      }
    },
  },
}
</script>
<template>
  <div
    class="flex bg-white border py-1.5 px-1 rounded-lg font-nunito -ml-px h-10.5"
  >
    <div class="flex flex-row w-full justify-start p-1">
      <div v-if="ledgerAccount.number" class="flex inline text-white ml-1 mr-2">
        <span
          class="pt-1 pb-2px mr-px font-bold text-2xs"
          v-for="(item, idx) in getNumberOfAccountsByParts(ledgerAccount)"
          :class="[
            'bg-ledgerAccountTypes-' + ledgerAccount.ledgerAccountType.type,
            {
              'rounded-tl rounded-bl pl-2 pr-1': idx === 0,
              'pl-1 pr-2 rounded-r':
                idx === getNumberOfAccountsByParts(ledgerAccount).length - 1,
              'pl-2 pr-2':
                idx !== 0 &&
                idx !== getNumberOfAccountsByParts(ledgerAccount).length - 1,
            },
          ]"
          :key="idx"
        >
          {{ item }}
        </span>
      </div>
      <!-- :ref="ledgerAccount.ledgerAccountId" -->

      <input
        required
        v-model="ledgerAccount.name"
        ref="inputLedgerAccount"
        class="flex w-full inline-block outline-none resize-none text-xs"
        type="text"
        autofocus
        @focus="onFocus"
        @keyup="filteredList"
        @keydown.esc.prevent.stop="closeList"
        @keydown.tab="closeList"
      />
      <!-- @blur="closeList" -->
    </div>
    <transition name="fade">
      <div
        v-if="ledgerAccountsTemp"
        class="border absolute overflow-x-hidden overflow-y-auto AccountContainer w-auto z-9 bg-white mt-8 -ml-2 max-h-50 max-w-xl min-w-82"
      >
        <GXLedgerAccountSearchItem
          v-if="ledgerAccountsTemp"
          :ledgerAccounts="ledgerAccountsTemp"
          @onSelected="onSelected"
        />
      </div>
    </transition>
  </div>
</template>

